<template>
  <div class="container">
    <h1>Login</h1>
    <div class="form-group">
      <label>Correo electronico</label>
      <input type="email" v-model="formdata.email">
    </div>
    <div class="form-group">
      <label>Contraseña</label>
      <input type="password" v-model="formdata.password">
    </div>
    <div class="form-group">
      <button class="btn btn-primary" @click.prevent="onLogin">Log In</button>
    </div>
  </div>
</template>

<script>
import {getLogin} from '@/services/Reqres'
export default {
  name: 'LoginView',
  data () {
    return {
      formdata: {
        email: '',
        password: ''
      }
    }
  },
  methods: {
    onLogin () {
      getLogin(this.formdata.email, this.formdata.password).then((response) => {
        this.$store.dispatch('setLogin', response.data.token)
      }).catch((err) => console.error(err))
    }
  }
}
</script>



