import axios from 'axios'
const API_URL = 'https://reqres.in/api'

/**
 * Function to return login session
 * @param {String} email
 * @param {String} password
 * @return AxiosPromise
 */
export function getLogin (email, password) {
  return axios.post(`${API_URL}/login`, {
    email,
    password
  })
}



