<template>
  <div class="container">
    <h1>List users</h1>
    <h1 v-if="loading">Cargando datos...</h1>
    <ul v-else>
      <li class="mb-3" v-for="user in users" :key="user.id">
        <img :src="user.avatar" width="35">
        {{user.first_name}}
      </li>
    </ul>
  </div>
</template>

<script>
import {getUsersGithub} from '@/services/Github'
export default {
  name: 'ListUsers',
  created () {
    this.loading = true
    getUsersGithub().then((response) => {
      this.users = response.data.data
      this.loading = false
    }).catch((err) => {
      console.error(err)
      this.loading = false
    })
  },
  data () {
    return {
      users: [],
      loading: false
    }
  }
}
</script>



